ue=\"room\"\n                           class=\"sm:rounded-lg\"\n                           >\n\n                                {{room.name}}\n\n                   </option>\n            </template>\n\n                </select>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport {VueSelect} from 'vue-select'\nimport 'vue-select/dist/vue-select.css';\nimport JetButton from '@/Jetstream/Button.vue';\nexport default {\n    name: \"chatRoomSelection\",\n    props: ['rooms', 'currentRoom'],\n    components: {\n        'v-select': VueSelect,\n        JetButton\n    },\n    data: function (){\n        return {\n            selected: '',\n        }\n    },\n    created() {\n        this.selected = this.currentRoom;\n    }\n\n}\n</script>\n\n<style scoped>\n\n.ChatSelection{\n    background-image: none;\n    padding-right: 0;\n    border: unset;\n}\n</style>\n"],"sourceRoot":""}]);
